@use 'base';

section
{
    position: absolute;
    top: 5%;
    left: 50%;
    height: 40%;
    width: 70%;
    transform: translate(-50%);
    z-index: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    
    h1
    {
        font-weight: 400;
        text-shadow: 0px 0px 6px rgba(66, 68, 90, 1);
        color: white;
        font-size: 2.3rem;
    }
    .textarea
    {
        height: 15%;
        width: 40%;
        border-radius: 15px;
        box-shadow: 0px 0px 19px -7px rgba(66, 68, 90, 1);
        display: flex;
        justify-content: flex-end;
        align-items: center;
        background: white;
        overflow: hidden;
        input
        {
            
            border: none;
            color: black;
            height: 100%;
            width: 90%;
            padding: 20px;
            font-size: 18px;
        }
        ::placeholder
        {
            color: base.$secound-text-color;
            font-family: 'Rubik', sans-serif;
        }
        button
        {
            height: 100%;
            width: 10%;
            background: black;
            cursor: pointer;
        }
    }
}