@use 'base';

.background {
    position: relative;
    z-index: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100vw;
    height: 100vh;
    overflow: hidden;

    img {
        height: 50%;
        width: 100%;
        pointer-events: none;
    }

    #map {
        width: 110%;
        height: 100%;

    }

}
