@use 'base';

.content {
    width: 100%;
    height: 50%;
    background: white;
    border-radius: 20px;
    box-shadow: 0px 0px 19px -7px rgba(66, 68, 90, 1);
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    padding: 30px;

    article {
        height: 100%;
        width: 25%;
        position: relative;
        display: flex;
        flex-direction: column;
        padding: 10px 0 10px 30px;
    }

    article::after {
        content: "";
        display: block;
        height: 60%;
        width: 1px;
        position: absolute;
        top: 50%;
        left: 100%;
        transform: translate(0%, -50%);
        background: hsla(0, 0%, 59%, 0.37);
    }

    article:last-child::after {
        display: none;
    }

    h2 {
        color: base.$secound-text-color;
        font-size: 0.9rem;
        font-weight: 500;
        text-transform: uppercase;
    }
    span
    {
        font-size: 1.5rem;
        font-weight: 500;
        margin-top: 15px;
        color: base.$primary-text-color;
        overflow-wrap: break-word;
        min-height: 40%;
        width: 80%;
    }
}